import React from "react";
import Uploady from "@rpldy/uploady";
import UploadButton from "@rpldy/upload-button";
import "./Upload.css";
import Tippy from "@tippyjs/react";
import "tippy.js/dist/tippy.css"; //
import { useState } from "react";
import {
  useItemFinishListener,
  useItemProgressListener,
  useItemStartListener,
} from "@rpldy/uploady";
export default function Upload() {
  const [disabled, setDisabled] = useState(false);
  const [img, setImg] = useState();
  const [response,setResponse] = useState()

  const UploadImageResponse = () => {
    useItemStartListener((item) => {
      let dataUrl = URL.createObjectURL(item.file);
      setImg(dataUrl);
    });
    useItemFinishListener(async (item) => {
      let tmpRmBg = "data:image/png;base64," + item.uploadResponse.data.predict;
      setResponse(tmpRmBg);
    });
    return (
      <Tippy content="Try now" visible={!disabled} disabled={false}>
        <UploadButton
          onClick={() => setDisabled(true)}
          className="uploader"
        />
      </Tippy>
    );
  };
  return (
    <Uploady destination={{ url: "" }}>
      <UploadImageResponse />
    </Uploady>
  );
}
